import { useEditor, EditorContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import { useState, useRef } from "react";
import Image from "next/image";

function AddBlogForm() {
  const [title, setTitle] = useState("");
  const [coverImage, setCoverImage] = useState(null);
  const [coverImagePreview, setCoverImagePreview] = useState(null);
  const [category, setCategory] = useState("");
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState("");
  const [author, setAuthor] = useState("Ahmed Hassan (Admin)");
  const [showAuthorBio, setShowAuthorBio] = useState(true);
  const [metaTitle, setMetaTitle] = useState("");
  const [metaDescription, setMetaDescription] = useState("");
  const [visibility, setVisibility] = useState("Public");
  const [publishStatus, setPublishStatus] = useState("Immediately");
  const fileInputRef = useRef(null);

  const editor = useEditor({
    extensions: [StarterKit],
    content: "<p>Start writing your Egypt guide...</p>",
    editorProps: {
      attributes: {
        class:
          "prose prose-sm sm:prose lg:prose-lg xl:prose-2xl focus:outline-none min-h-[300px] p-4",
      },
    },
  });

  const handleCoverImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setCoverImage(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setCoverImagePreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleAddTag = (e) => {
    if (e.key === "Enter" && tagInput.trim()) {
      e.preventDefault();
      if (!tags.includes(tagInput.trim())) {
        setTags([...tags, tagInput.trim()]);
      }
      setTagInput("");
    }
  };

  const handleRemoveTag = (tagToRemove) => {
    setTags(tags.filter((tag) => tag !== tagToRemove));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const blogData = {
      title,
      content: editor?.getHTML(),
      coverImage,
      category,
      tags,
      author,
      showAuthorBio,
      metaTitle,
      metaDescription,
      visibility,
      publishStatus,
    };

  };

  return (
    <section className="max-w-7xl mx-auto py-8 px-4">
      <div className="bg-white rounded-lg shadow-sm p-6 mb-4 flex justify-between items-center">
        <div>
          <p className="text-sm text-gray-500">Draft Mode</p>
          <p className="text-xs text-gray-400">Auto-saved at 2:45 PM</p>
        </div>
        <div className="flex gap-3">
          <button
            type="button"
            className="px-4 py-2 bg-amber-100 text-amber-700 rounded-lg hover:bg-amber-200 transition"
          >
            Save Draft
          </button>
          <button
            type="button"
            className="px-4 py-2 bg-amber-500 text-white rounded-lg hover:bg-amber-600 transition"
          >
            Preview
          </button>
          <button
            type="submit"
            onClick={handleSubmit}
            className="px-4 py-2 bg-gray-800 text-white rounded-lg hover:bg-gray-900 transition"
          >
            Publish
          </button>
        </div>
      </div>

      <div className="flex flex-col lg:flex-row gap-6">
        {/* Main Content Area */}
        <div className="flex-1">
          <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="Enter your story title..."
              className="w-full text-4xl font-serif border-none focus:outline-none mb-4"
            />

            {/* Editor Toolbar */}
            <div className="border-t border-b border-gray-200 py-3 mb-4 flex items-center gap-2 flex-wrap">
              <button
                type="button"
                onClick={() => editor?.chain().focus().toggleBold().run()}
                className={`p-2 rounded hover:bg-gray-100 ${editor?.isActive("bold") ? "bg-gray-200" : ""}`}
                title="Bold"
              >
                <strong>B</strong>
              </button>
              <button
                type="button"
                onClick={() => editor?.chain().focus().toggleItalic().run()}
                className={`p-2 rounded hover:bg-gray-100 ${editor?.isActive("italic") ? "bg-gray-200" : ""}`}
                title="Italic"
              >
                <em>I</em>
              </button>
              <button
                type="button"
                onClick={() => editor?.chain().focus().toggleBulletList().run()}
                className={`p-2 rounded hover:bg-gray-100 ${editor?.isActive("bulletList") ? "bg-gray-200" : ""}`}
                title="Bullet List"
              >
                ‚ò∞
              </button>
              <button
                type="button"
                onClick={() =>
                  editor?.chain().focus().toggleOrderedList().run()
                }
                className={`p-2 rounded hover:bg-gray-100 ${editor?.isActive("orderedList") ? "bg-gray-200" : ""}`}
                title="Numbered List"
              >
                ‚â°
              </button>
              <button
                type="button"
                onClick={() => editor?.chain().focus().toggleBlockquote().run()}
                className={`p-2 rounded hover:bg-gray-100 ${editor?.isActive("blockquote") ? "bg-gray-200" : ""}`}
                title="Quote"
              >
                ‚ùù
              </button>
              <button
                type="button"
                onClick={() => editor?.chain().focus().toggleCodeBlock().run()}
                className={`p-2 rounded hover:bg-gray-100 ${editor?.isActive("codeBlock") ? "bg-gray-200" : ""}`}
                title="Code Block"
              >
                {"</>"}
              </button>
              <button
                type="button"
                onClick={() => editor?.chain().focus().undo().run()}
                className="p-2 rounded hover:bg-gray-100"
                title="Undo"
              >
                ‚Ü∂
              </button>
              <button
                type="button"
                onClick={() => editor?.chain().focus().redo().run()}
                className="p-2 rounded hover:bg-gray-100"
                title="Redo"
              >
                ‚Ü∑
              </button>
            </div>

            {/* Editor Content */}
            <EditorContent editor={editor} />

            <div className="mt-4 text-sm text-gray-500">
              Words: {editor?.storage.characterCount?.words() || 0} ¬∑
              Characters: {editor?.getText().length || 0}
            </div>
          </div>
        </div>

        {/* Sidebar */}
        <div className="lg:w-96 space-y-6">
          {/* Featured Media */}
          <div className="bg-gray-800 rounded-lg p-6 text-white">
            <h3 className="text-lg font-semibold mb-4">FEATURED MEDIA</h3>
            <div className="relative aspect-video bg-gray-700 rounded-lg overflow-hidden">
              {coverImagePreview ? (
                <Image
                  src={coverImagePreview}
                  alt="Cover"
                  fill
                  className="object-cover"
                />
              ) : (
                <div className="absolute inset-0 flex items-center justify-center">
                  <div className="text-center">
                    <div className="text-4xl mb-2">üèúÔ∏è</div>
                    <p className="text-sm text-gray-400">No image selected</p>
                  </div>
                </div>
              )}
              <button
                type="button"
                onClick={() => fileInputRef.current?.click()}
                className="absolute bottom-4 left-1/2 transform -translate-x-1/2 px-4 py-2 bg-gray-900/80 rounded-lg hover:bg-gray-900 transition"
              >
                Set Cover Image
              </button>
              <input
                ref={fileInputRef}
                type="file"
                accept="image/*"
                onChange={handleCoverImageChange}
                className="hidden"
              />
            </div>
          </div>

          {/* Taxonomy */}
          <div className="bg-gray-800 rounded-lg p-6 text-white">
            <h3 className="text-lg font-semibold mb-4">TAXONOMY</h3>

            <div className="mb-4">
              <label className="block text-sm mb-2">Categories</label>
              <select
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                className="w-full p-2 bg-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
              >
                <option value="">Select Category</option>
                <option value="Travel">Travel</option>
                <option value="Giza">Giza</option>
                <option value="History">History</option>
                <option value="Culture">Culture</option>
                <option value="Food">Food</option>
              </select>
            </div>

            <div>
              <label className="block text-sm mb-2">Tags</label>
              <div className="flex flex-wrap gap-2 mb-2">
                {tags.map((tag) => (
                  <span
                    key={tag}
                    className="px-3 py-1 bg-amber-500 rounded-full text-sm flex items-center gap-2"
                  >
                    {tag}
                    <button
                      type="button"
                      onClick={() => handleRemoveTag(tag)}
                      className="hover:text-gray-200"
                    >
                      √ó
                    </button>
                  </span>
                ))}
              </div>
              <input
                type="text"
                value={tagInput}
                onChange={(e) => setTagInput(e.target.value)}
                onKeyDown={handleAddTag}
                placeholder="Add tag..."
                className="w-full p-2 bg-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
              />
            </div>
          </div>

          {/* Publishing Info */}
          <div className="bg-gray-800 rounded-lg p-6 text-white">
            <h3 className="text-lg font-semibold mb-4">PUBLISHING INFO</h3>

            <div className="mb-4">
              <label className="block text-sm mb-2">Post Author</label>
              <select
                value={author}
                onChange={(e) => setAuthor(e.target.value)}
                className="w-full p-2 bg-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
              >
                <option value="Ahmed Hassan (Admin)">
                  Ahmed Hassan (Admin)
                </option>
              </select>
            </div>

            <div className="flex items-center justify-between">
              <label className="text-sm">Show Author Bio</label>
              <button
                type="button"
                onClick={() => setShowAuthorBio(!showAuthorBio)}
                className={`relative w-12 h-6 rounded-full transition ${
                  showAuthorBio ? "bg-amber-500" : "bg-gray-600"
                }`}
              >
                <span
                  className={`absolute top-1 left-1 w-4 h-4 bg-white rounded-full transition-transform ${
                    showAuthorBio ? "translate-x-6" : ""
                  }`}
                />
              </button>
            </div>
          </div>

          {/* SEO Settings */}
          <div className="bg-gray-800 rounded-lg p-6 text-white">
            <h3 className="text-lg font-semibold mb-4">SEO SETTINGS</h3>

            <div className="mb-4">
              <label className="block text-sm mb-2">
                Meta Title{" "}
                <span className="text-gray-400 float-right">0 / 60</span>
              </label>
              <input
                type="text"
                value={metaTitle}
                onChange={(e) => setMetaTitle(e.target.value)}
                placeholder="Search engine title"
                maxLength={60}
                className="w-full p-2 bg-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
              />
            </div>

            <div>
              <label className="block text-sm mb-2">
                Meta Description{" "}
                <span className="text-gray-400 float-right">0 / 160</span>
              </label>
              <textarea
                value={metaDescription}
                onChange={(e) => setMetaDescription(e.target.value)}
                placeholder="Brief summary for search results..."
                maxLength={160}
                rows={3}
                className="w-full p-2 bg-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500 resize-none"
              />
            </div>
          </div>

          {/* Publish Settings */}
          <div className="bg-gray-800 rounded-lg p-6 text-white space-y-4">
            <div className="flex items-center justify-between">
              <span className="text-sm">üëÅÔ∏è Visibility: {visibility}</span>
              <button
                type="button"
                className="text-amber-500 text-sm hover:underline"
              >
                Edit
              </button>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm">üìÖ Publish: {publishStatus}</span>
              <button
                type="button"
                className="text-amber-500 text-sm hover:underline"
              >
                Edit
              </button>
            </div>
            <button
              type="button"
              className="w-full py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition"
            >
              üóëÔ∏è Move to Trash
            </button>
          </div>
        </div>
      </div>
    </section>
  );
}

export default AddBlogForm;
